import React from 'react';

function Property(props) {

    const properties = (()=> {
        const propertiesJSX = [];

        for (let property in props.properties) {
            const propertiesValueJSX = [];

            for (let propertyValue of props.properties[property]) {
                propertiesValueJSX.push(
                    <div className="font-families__property-value">{propertyValue}</div>
                );
            }

            propertiesJSX.push(
                <div className="font-families__font-property">
                    <div className="font-families__property-title">{property}:</div>
                    <div className="font-families__property-values">
                        {propertiesValueJSX}
                    </div>
                </div>
            )
        }

        return propertiesJSX;
    })();

    return (
        <div className={props.className}>
            {properties}
        </div>
    )
}

export default Property;